import _get from 'lodash/get';
import Api from '../utils/api';

const Tickets = {
  getTickets({ buildingId, assigneeId, categoryId }) {
    return Api.get('/tickets', {
      building_id: buildingId,
      assignee_id: assigneeId,
      category_id: categoryId
    });
  },
  getTicket(ticketId) {
    if (ticketId) {
      return Api.get(`/tickets/${ticketId}`);
    }
    return null;
  },
  updateTicket(ticket) {
    const body = {
      ...ticket,
      building_id: _get(ticket, 'building.id'),
      assignee_id: _get(ticket, 'assignee.id'),
      category_id: _get(ticket, 'category.id')
    };
    return Api.put(`/tickets/${_get(ticket, 'id')}`, body);
  },
  moveTicket({ id, status, position, building_id }) {
    return Api.put(`/tickets/${id}/move`, { status, position, building_id });
  },
  postTicket(ticket) {
    return Api.post('/tickets', ticket);
  },
  getAssignables(buildingId) {
    return Api.get('/tickets/assignables', { building_id: buildingId });
  }
};

export default Tickets;
