import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _get from 'lodash/get';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import TicketsService from '../../../services/tickets';
import Kanban from '../../../components/Kanban';
import Loading from '../../../components/Loading';
import withRequest from '../../../utils/with-request';
import TicketCard from '../TicketCard';
import styles from './styles';

const TicketBoard = memo(({
  tickets, categories, assignees, loading,
  moveTicketFetch, onCloseCardModal, lockBoard
}) => {
  if (loading) {
    return <Loading />;
  }
  return (
    <Kanban
      data={tickets || []}
      disableDragDrop={lockBoard}
      renderCard={(t) => (
        <TicketCard
          ticket={t}
          assignees={assignees}
          categories={categories}
          onCloseModal={onCloseCardModal}
          disableEdit={lockBoard}
        />
      )}
      onDragEnd={({ draggableId, destination }) => {
        const params = {
          variables: {
            id: draggableId,
            status: _get(destination, 'droppableId'),
            position: _get(destination, 'index')
          }
        };
        moveTicketFetch(false, '', null, params);
      }}
      columnStyle={styles.column}
      columnHeaderStyle={styles.header}
      columnTitleStyle={styles.headerTitle}
      columnWrapperStyle={styles.wrapper}
    />
  );
});

TicketBoard.propTypes = {
  tickets: PropTypes.array,
  categories: PropTypes.array,
  assignees: PropTypes.array,
  lockBoard: PropTypes.bool,
  loading: PropTypes.bool,
  onCloseCardModal: PropTypes.func,
  moveTicketFetch: PropTypes.func
};


const withMoveTicket = withRequest(TicketsService.moveTicket, {
  name: 'moveTicket',
  init: false
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withMoveTicket
)(TicketBoard);
