import React, { memo } from 'react';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { compose, withState } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import SettingsMenu from '../../components/SettingsMenu';
import withRequest from '../../utils/with-request';
import BuildingsMembersService from '../../services/buildings-members';
import Session from '../../utils/session';
import MembersTable from './MembersTable';
import styles from './styles';

const Members = memo(({
  buildingMembersData, buildingMembersLoading, buildingMembersError,
  alert, setAlert, buildingMembersFetch, classes
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox message={alertMsg} type={alertType} onClose={onClearAlert} />;
  }
  const enterpriseUser = Session.getUserData();
  const loggedMemberId = _get(enterpriseUser, 'member.id');
  const members = _get(buildingMembersData, 'members');
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} error={buildingMembersError} />
      <section>
        <div className={classes.container}>
          <div className={classes.headerView}>
            <aside>
              <Typography variant="h5">
                <b>Configuraes</b>
              </Typography>
            </aside>
          </div>
        </div>
        <div className={classes.container}>
          <SettingsMenu />
        </div>
        <div className={classes.container}>
          <MembersTable
            loggedMemberId={loggedMemberId}
            refetch={() => buildingMembersFetch(false)}
            setAlert={setAlert}
            members={members}
            loading={buildingMembersLoading}
          />
        </div>
      </section>
    </div>
  );
});

Members.propTypes = {
  classes: PropTypes.object,
  buildingMembersLoading: PropTypes.bool,
  buildingMembersError: PropTypes.object,
  buildingMembersData: PropTypes.object,
  buildingMembersFetch: PropTypes.func,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

const withMembers = withRequest(BuildingsMembersService.getBuildingsMembers, {
  name: 'buildingMembers'
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withMembers
)(Members);
