import Api from '../utils/api';

const Members = {
  getMembers() {
    return Api.get('/members');
  },
  updateBuildingRole({ memberId, buildingId, role }) {
    return Api.put(
      '/enterprise_users/update_role',
      { member_id: memberId, building_id: buildingId, role }
    );
  }
};

export default Members;
